import { useEffect, useState } from "react";
import styles from "./styles/app.module.css";

import { data } from "../src/data/kaijuList";
import Question from "./components/Question";
import AnswerPicker from "./components/AnswerPicker";
import Curtain from "./components/Curtain";
import Results from "./components/Results";
import background from "./data/images/monsterIsland.jpg";
import mainImage from "./data/images/Shimizu_BDPGodzilla_Lg.jpg";

export default function App() {
  const [question, setQuestion] = useState();
  const [trackerList, setTrackerList] = useState(
    data.map((currElement, index) => {
      return index;
    })
  );
  const [answer, setAnswer] = useState();

  const [score, setScore] = useState([]);

  function handleTurn() {
    let randomIndex = Math.floor(Math.random() * trackerList?.length);
    let questionIndex = trackerList[randomIndex];
    setQuestion(data[questionIndex]);
    setTrackerList((currentList) =>
      currentList.filter((elem, i) => i !== randomIndex)
    );
    // allows rerender when answer index doesn't change
    setAnswer(null);
  }

  function handleSelect(answerIndex) {
    setAnswer(answerIndex);
    animateQuestion();
  }

  useEffect(() => {
    if (question && answer !== null) {
      if (data[answer]?.name === question.name) {
        //console.log("correct");
        setScore([...score, { name: question.name, correct: true }]);
      } else {
        //console.log("wrong");
        setScore([...score, { name: question.name, correct: false }]);
      }
      handleTurn();
    }
  }, [answer]);

  function reset() {
    setTrackerList(
      data.map((currElement, index) => {
        return index;
      })
    );
    setScore([]);
  }

  const [questionAnimate, setQuestionAnimate] = useState(styles.slideIn);

  async function animateQuestion() {
    setQuestionAnimate("slideOut");
    let promise = new Promise((resolve, reject) => {
      setTimeout(() => {
        return resolve("done!");
      }, 1000);
    });
    // wait until the promise resolves (*)
    let result = await promise;
    // "done!"
    setQuestionAnimate("slideIn");
  }

  return (
    <>
      <div className={styles.mainContainer}>
        <div className={styles.bgContainer}>
          <img src={background} alt="monster island" />
        </div>

        {/* LANDING */}
        {!question && trackerList.length > 0 && (
          <>
            <Curtain down={true} />
            <h1 className={styles.titleAnim}>CALL OF THE KAIJU</h1>
            <div className={styles.mainImage}>
              <img src={mainImage} alt="Godzilla" />
            </div>
            <p>Can you identify every kaiju roar?</p>
            <button type="button" onClick={handleTurn}>
              PLAY
            </button>
          </>
        )}

        {/* GAMEPLAY */}

        {question && (
          <>
            <Curtain down={true} />
            <Question
              text={question?.name}
              questionId={data.length - trackerList.length}
              roar={question?.roar}
              animate={questionAnimate}
            />
          </>
        )}
        {question && <AnswerPicker handleSelect={handleSelect} />}

        {/* END */}
        {!question && trackerList.length === 0 && (
          <>
            <Results score={score} />
            <button type="button" onClick={reset}>
              HOME
            </button>
          </>
        )}
      </div>
    </>
  );
}
