import { useState } from "react";

import Image from "./Image";

import styles from "../styles/answerPicker.module.css";

import { data } from "../data/kaijuList";

export default function AnswerPicker(props) {
  const list = [...data];

  const [answerIndex, setAnswerIndex] = useState(0);

  function answerNavForward() {
    //console.log((answerIndex + 1) % list.length);
    setAnswerIndex((answerIndex + 1) % list.length);
  }

  function answerNavBackward() {
    //console.log((answerIndex + 1) % list.length);
    setAnswerIndex(answerIndex === 0 ? list.length - 1 : answerIndex - 1);
  }

  function handleSelect() {
    props.handleSelect(answerIndex);
  }

  const backArrow = "<";
  const forwardArrow = ">";

  return (
    <div className={styles.mainContainer}>
      <h2>{list[answerIndex].name}</h2>
      <Image src={list[answerIndex].image} alt={list[answerIndex].name} />
      <div className={styles.buttonsContainer}>
        <div>
          <button onClick={answerNavBackward}>{backArrow}</button>
        </div>
        <div>
          <button onClick={handleSelect}>SELECT</button>
        </div>
        <div>
          <button onClick={answerNavForward}>{forwardArrow}</button>
        </div>
      </div>
    </div>
  );
}
