import { useState } from "react";
import styles from "../styles/question.module.css";
import AudioVisualizer from "./AudioVisualizer";

import Godzilla from "../data/sounds/247819-eed21ff3-3a39-4790-82fd-870f825ece29.mp3";

export default function Question(props) {
  let roar = new Audio(props.roar);
  let animate = styles.slideIn;

  if (props.animate === "slideIn") {
    animate = styles.slideIn;
  } else if (props.animate === "slideOut") {
    animate = styles.slideOut;
  }

  function handlePlay() {
    roar.play();
  }

  return (
    <div className={styles.mainContainer}>
      <div className={animate}>
        <h2>SOUND {props.questionId}</h2>
        <AudioVisualizer audio={Godzilla} />
        <button type="button" onClick={handlePlay}>
          Play
        </button>
      </div>
    </div>
  );
}
