import styles from "../styles/results.module.css";

export default function Results(props) {
  let correctMark = <div className={styles.correctMark}></div>;
  let wrongMark = <div className={styles.incorrectMark}></div>;
  let elements = props.score.map((question, index) => (
    <div className={styles.answerContainer} key={index}>
      <h2>{question.name}</h2>
      {question.correct ? correctMark : wrongMark}
    </div>
  ));

  let total = 0;

  for (let step = 0; step < props.score.length; step++) {
    if (props.score[step].correct) {
      total += 1;
    }
  }
  return (
    <div className={styles.mainContainer}>
      <h2>SCORE: {total}</h2>
      {elements}
    </div>
  );
}
